.contact-card{
    width: 30%;
    text-align: center;
    background: #d6d3d3;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    height: 300px;
}
.subtitle{
    margin-top: 10%;
}
.contact-paragraph{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 90%;
}
.contact-info {
    font-size: 1.3vw;
}

@media only screen and (max-width: 768px) {
    .contact-card {
      width: 90%;
      margin: 20px 0;
      height: 250px;
    }
    }
    
    @media only screen and (max-width: 600px) {
      .contact-card {
        width: 100%;
      }
    }